import {
	mapState
} from 'vuex'
import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiBadge from "@/components/badge/badge"
import tuiNomore from "@/components/nomore/nomore"
import tuiButton from "@/components/button/button"
import tuiTopDropdown from "@/components/top-dropdown/top-dropdown"
import tuiBottomPopup from "@/components/bottom-popup/bottom-popup"
import tuiNumberbox from "@/components/numberbox/numberbox"
import tTable from '@/components/t-table/t-table.vue'
import tTr from '@/components/t-table/t-tr.vue'
import tTd from '@/components/t-table/t-td.vue'
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiBadge,
		tuiNomore,
		tuiButton,
		tuiTopDropdown,
		tuiBottomPopup,
		tuiNumberbox,
		tTable,
		tTr,
		tTd
	},
	data() {
		return {
			detailTitlt: '',
			goodsInfo: {},
			showCont: true,
			height: 64, //header高度
			top: 0, //标题图标距离顶部距离
			scrollH: 0, //滚动总高度
			opcity: 0,
			iconOpcity: 0.5,
			banner: [],
			bannerIndex: 0,
			topMenu: [{
				icon: "home",
				text: "首页",
				size: 23,
				badge: 0
			}, 
			{
				icon: "people",
				text: "我的",
				size: 26,
				badge: 0
			}, 
			{
				icon: "cart",
				text: "购物车",
				size: 23,
				badge: 2
			}
			],
			menuShow: false,
			popupShow: false,
			value: 1,
			collected: false,
			typeStr: '',
			subGoodsId: ''
		}
	},
	methods: {
		handleChooseAttrs(e, j){
			this.goodsInfo.attrs[j].selectAttributeValue = e;
			let typeStr=this.goodsInfo.attrs.map(item=>{
				return item.selectAttributeValue;
			}).join(",");
			this.goodsInfo.subGoods.map(item=>{
				if(item.attributeKey === typeStr){
					this.subGoodsId = item.subGoodsId
				}
			})
			uni.setStorageSync('goodsId', this.subGoodsId);
			this.getGoodsDetail();
			this.showPopup();
		},
		bannerChange: function(e) {
			this.bannerIndex = e.detail.current
		},
		previewImage: function(e) {
			let picArray = [];
			this.banner.map(item => {
				picArray.push(item.url);
			});
			let index = e.currentTarget.dataset.index;
			uni.previewImage({
				current: this.banner[index].url,
				urls: picArray
			})
		},
		handleChangeCont(num){
			switch (num) {
				case 0:
					this.showCont=true;
					break;
				case 1:
					this.showCont=false;
					break;
				// default:
				// 	this.showCont=true;
			}
		},
		back: function() {
			uni.navigateBack()
		},
		openMenu: function() {
			this.menuShow = true
		},
		closeMenu: function() {
			this.menuShow = false
		},
		showPopup: function() {
			this.popupShow = true
		},
		hidePopup: function() {
			this.popupShow = false
		},
		change: function(e) {
			this.value = e.value
		},
		handleAddCart(mallGoodsId){
			if(!Boolean(this.hasLogin)){
				uni.navigateTo({
				    url: '/pages/login/login'
				});
				return;
			}else{
				this.$http.post('/mall/app/car/add', {
					...this.baseInfo,
					accountId: this.userInfo.accountId,
					goodsCount: this.value,
					mallGoodsId: mallGoodsId,
				})
				.then( res => {
					if(res.code == 0){
						this.getGoodsDetail();
						uni.showToast({
							icon: 'none',
							title: '商品添加成功',
						});
					}else{
						console.log('productDetail.vue-- add接口添加购物车失败');
					}
				})
				.catch( err => {
					console.log('productDetail.vue-- add接口添加购物车错误');
				})
			}
			this.hidePopup();
		},
		linkToPayment(id){
			if(!this.hasLogin){
				uni.navigateTo({
				    url: '/pages/login/login'
				});
			}else{
				let tmpList = [];
				tmpList.push({
					mallGoodsId: id,
					goodsCount: this.value
				})
				uni.setStorage({
					key:'buyList',
					data:tmpList,
					success: () => {
						uni.navigateTo({
							url:'/pages/payment/payment'
						})
					}
				})
			}
			this.hidePopup();
		},
		linkToIndex(){
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		linkToUser(){
			uni.reLaunch({
				url: '/pages/user/user'
			})
		},
		linkToCart(){
			if(!this.hasLogin){
				uni.showToast({
					icon: 'none',
					title: '请先去登陆吧~'
				})
			}else{
				uni.reLaunch({
					url: '/pages/car/car'
				})
			}
		},
		// onBridgeReady() {
		// 	WeixinJSBridge.call('hideOptionMenu');
		// },
		// weixinSendAppMessage(title,desc,link,imgUrl){
		// 	alert('weixinSendAppMessage开始---------------')
		// 	WeixinJSBridge.invoke('sendAppMessage',{
		// 	//”appid”:appId,
		// 	'img_url':'',
		// 	//”img_width”:”640″,
		// 	//”img_height”:”640″,
		// 	'link':'https://www.baidu.com/',
		// 	'desc':'描述',
		// 	'title':'123123'
		// 	});
		// },
		getGoodsDetail(){
			let goodsId=uni.getStorageSync('goodsId');
			this.$http.post('/mall/app/goods/detail', {
				...this.baseInfo,
				goodsId: goodsId,
				accountId: this.userInfo.accountId || ''
			})
			.then( res => {
				if(res.code == 0){
					this.goodsInfo=res.result;
					this.banner=res.result.pics;
					this.goodsInfo.details=this.goodsInfo.details.replace(/<img /g, '<img class="rich_img" ');
				}else{
					console.log('productDetail.vue-- detail接口获取数据失败');
				};
				// if (typeof WeixinJSBridge == "undefined") {
				//     if (document.addEventListener) {
				//         document.addEventListener('WeixinJSBridgeReady', this.onBridgeReady(), false);
				//     } else if (document.attachEvent) {
				//         document.attachEvent('WeixinJSBridgeReady', this.onBridgeReady());
				//         document.attachEvent('onWeixinJSBridgeReady', this.onBridgeReady());
				//     }
				// } else {
				//     this.onBridgeReady();
				// }
			})
			.catch( err => {
				console.log('productDetail.vue-- detail接口获取数据错误');
			})
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo', 'hasLogin'])
	},
	onLoad: function(options) {
		uni.setStorageSync('goodsId', options.goodsId);
		this.getGoodsDetail();
		
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







	
		uni.getSystemInfo({
			success: (res) => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? (obj.top + obj.height + 8) : (res.statusBarHeight + 44);
				this.top = obj.top ? (obj.top + (obj.height - 32) / 2) : (res.statusBarHeight + 6);
				this.scrollH = res.windowWidth
			}
		})
	},
	onPageScroll(e) {
		let scroll = e.scrollTop <= 0 ? 0 : e.scrollTop;
		let opcity = scroll / this.scrollH;
		if (this.opcity >= 1 && opcity >= 1) {
			return;
		}
		this.opcity = opcity;
		this.iconOpcity = 0.5 * (1 - opcity < 0 ? 0 : 1 - opcity)
	}
}
